//
<template>
    <div class="todo-item" v-bind:class="{'is-complete': todo.completed}">
        <p>
            <input type="checkbox" v-on:change="markTodoCompleted(todo.id)">
            {{ todo.title }}
            <button class="del" v-on:click="deleteTodo(todo.id)">X</button>
        </p>
    </div>
</template>

<script>

import { mapActions } from 'vuex';

export default {
    name: 'TodoItem',
    props: [ "todo" ],
    methods: {
        ...mapActions([
            'markTodoCompleted',
            'deleteTodo'
        ])
    }
}
</script>

<style scoped>
    .todo-item {
        background: #f4f4f4;
        padding: 10px;
        border-bottom: 1px #ccc dotted;
    }

    .is-complete {
        text-decoration: line-through;
    }

    .del {
        background: #ff0000;
        color: #fff;
        border: none;
        padding: 5px 9px;
        border-radius: 50%;
        cursor: pointer;
        float: right;
    }
</style>
